:root {
  --border-radius: 5px;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    Noto Sans CJK TC, Source Han Sans TW, Microsoft Jhenghei, "微軟正黑體",
    Source Han Sans, Heiti TC, "黑體-繁", Lihei Pro, "儷黑 Pro", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  margin: 0;

  color: hsl(200, 20%, 30%);
  background-color: hsl(200, 20%, 90%);
}

code,
input {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

label,
h1,
h2,
h3 {
  font-size: small;
  font-weight: bold;
  text-transform: uppercase;
  padding: 0.25em 0.5em;
  margin: 0;
  opacity: 0.625;
  line-height: 1em;
}

input,
button {
  border: none;
  border-radius: var(--border-radius);
  padding: 0.25em 0.5em;
  line-height: 1em;
}

button {
  font-weight: bold;
  text-transform: capitalize;
  color: inherit;
  cursor: pointer;
  display: flex;
  align-items: center;
}

button svg {
  margin: 0 0.5em;
}

button svg:first-child {
  margin-left: 0;
}

button svg:last-child {
  margin-right: 0;
}

button.text {
  background: none;
  text-transform: inherit;
  padding: inherit;
}

hr {
  border: none;
  margin: 0.5rem 0;
}
